import { init, draw } from './canvas.js'
export default {
  name: 'VueLove',
  props: {
    pData: {
      call: {
        type: String
      },
      time: {
        type: String
      }
    }
  },
  data () {
    return {
      timeLen: [1000 * 60 * 60 * 24 * 365, 1000 * 60 * 60 * 24, 1000 * 60 * 60, 1000 * 60, 1000]
    }
  },
  computed: {
    meetDom () {
      return document.querySelector('#meet')
    },
    initDate () {
      return new Date(this.pData.time).getTime()
    }
  },
  mounted () {
    this.initDom()
    this.initEvent()
  },
  methods:{
    showTime () {
      const time = new Date().getTime() - this.initDate
      const years = time / this.timeLen[0] | 0
      const days = (time - (years * this.timeLen[0])) / (this.timeLen[1]) | 0
      const hours = (time - (years * this.timeLen[0]) - (days * this.timeLen[1])) / (this.timeLen[2]) | 0
      const minutes = (time - (years * this.timeLen[0]) - (days * this.timeLen[1]) - (hours * this.timeLen[2])) / (this.timeLen[3]) | 0
      const seconds = (time - (years * this.timeLen[0]) - (days * this.timeLen[1]) - (hours * this.timeLen[2]) - (minutes * this.timeLen[3])) / this.timeLen[4] | 0
      let meetTime = `${ days }天 ${ hours }小时 ${ minutes }分钟 ${ seconds }秒`
      if (years !== 0) meetTime = `${ years }年 ${ meetTime }`
      this.meetDom.innerText = meetTime
    },
    loop () {
      setTimeout(() => {
        this.initDom()
      }, 1000)
    },
    initDom () {
      this.showTime()
      this.loop()
    },
    loveing (x, y, dom, width, height) {
      const love = document.createElement('i')
      love.className = 'love'
      love.style.position = 'absolute'
      love.style.left = `${x}px`
      love.style.top = `${y}px`
      love.style.height = `${height * 2}px`
      love.style.width = `${width * 2}px`
      dom.appendChild(love)
      setTimeout(() => {
        dom.removeChild(love)
      }, 800)
    },
    initEvent () {
      // 50为桃心的宽度，50为桃心的高度
      const loveWidth = 50;
      const loveHeight = 50;
      const rangle = 25;

      // 鼠标点击的特效
      const container = document.querySelector('.container')
      document.body.addEventListener('touchstart', e => {
        const ran = Math.floor(Math.random() * rangle);
        const f = Math.random() >= 0.5 ? 1 : -1;

        const { pageX: oldX, pageY: oldY } = e.touches[0]
        const newX = oldX - (loveWidth + ran * f), newY = oldY - (loveHeight + ran * f)
        this.loveing(newX, newY, container, loveWidth + ran * f, loveHeight + ran * f)
      })
      document.body.addEventListener('touchend', e => {
        // 点击触摸事件之后禁止click事件
        if (e.preventDefault) e.preventDefault()
        else e.returnValue = false
      })
      document.body.addEventListener('click', e => {
        const ran = Math.floor(Math.random() * rangle);
        const f = Math.random() >= 0.5 ? 1 : -1;

        const { clientX: oldX, clientY: oldY } = e
        const newX = oldX - (loveWidth + ran * f), newY = oldY - (loveHeight + ran * f)
        this.loveing(newX, newY, container, loveWidth + ran * f, loveHeight + ran * f)
      })
      // canvas动画
      const canvas = document.querySelector('#canvas'), WIDTH = window.innerWidth, HEIGHT = window.innerHeight
      canvas.width = WIDTH
      canvas.height = HEIGHT
      const context = canvas.getContext('2d')
      const maxR = 15, minR = 5, circleArr = []
      let POINT = 35
      if (window.innerWidth < 780) POINT = 15
      else if (window.innerWidth < 1280) POINT = 25
      init({ POINT, WIDTH, HEIGHT, maxR, minR, circleArr })
      requestAnimationFrame(function fn() {
        draw({ context, canvas, POINT, WIDTH, HEIGHT, circleArr })
        requestAnimationFrame(fn)
      })
    }
  }
}


